@charset "utf-8";

body, h1, h2, h3, h4, h5, h6, p, ul, ol, table, dl, dd {
	margin: 0;
	padding: 0;
}
h1 {
	font-size: 16px;
}
a {
	text-decoration: none;
	color: #000;
}
ul, ol {
	list-style: none;
}
img {
	max-width: 100%;
	vertical-align: middle;
	border: none;
}
button {
	border: none;
	background-color: transparent;
}
body {
	margin: 0;
	padding: 0;
	font-family:  "Work Sans", "Noto Sans KR", "Roboto", "Noto Sans KR", sans-serif;  
	height: 100%;
	overflow-x: hidden;
	background-color: transparent; 
	transition: background-color ease-in-out 1s;
}
body.on {
	background-color: #000;
}
#tab {
	position: fixed;
	z-index: 9999999999;
	right: 30px;
	top: 28px;
	display: none;
}
#tab a img {
	display: block;
	width: 26px;
}
#m_close {
	position: fixed;
	z-index: 99999999999;
	right: 45px;
	top: 25px;
	display: none;
}
#m_close.active {
	display: block;
}
#m_close a img {
	width: 15px;
	height: 15px;
}
#m_gnb {
	display: none;
	position: fixed;
	z-index: 99999999;
	right: 0;
	top: 0;
	width:300px;
	height: 100%;
	background-color: #fff;
}
#m_gnb.active {
	display: block;
}
#m_gnb ul {
	display: flex;
	flex-direction: column;
	padding: 30px 20px;
	box-sizing: border-box;
}
#m_gnb ul li:nth-child(1) {
	margin-top: 4vh;
}
#m_gnb ul li {
	padding: 3.6vh 50px;
}
#m_gnb ul li a {
	padding: 5px 0;
	display: inline-block;
	position: relative;
	font-size: 1.25em;
	font-weight: bold;
	color: #666;
}
#m_gnb ul li a:hover {
	color: #000;
}
#m_gnb ul li a.active {
	color: #000;
}
#m_gnb ul li a.active::after {
	content: "";
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 2px;
	background-color: #000;
}

header {
	position: fixed;
	z-index: 1000000;
	left: 0;
	top: 0;
	width: 100%;
	height: 75px;
	color: #fff;
	background-color: #000;
	border-bottom: 1px solid rgba(255,255,255,.3);
}
header .inner {
	padding: 0 0 0 30px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	line-height: 75px;
}
header .inner .logo {
	width: 55px;
}
#gnb ul {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
#gnb ul li a {
	display: inline-block;
	padding: 0 40px;
	line-height: 75px;
	font-size: 0.913em;
	font-weight: 400;
	color: #fff;
	transition: all ease-in 0.5s;
}
#gnb ul li:hover a,
#gnb ul li a.active {
	background-color: #fff;
	color: #000;
}
#main {
	position: relative;
	height: 100vh;
	min-height: 30em;
	color: #fff;
}
#main .media {
	position: relative;
	width: 100%;
	height: 100%;
}
#main .media video {
	position: absolute;
	left: 0;
	top: 0;
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}
#main .controller {
	position: absolute;
	left: 50px;
	z-index: 10000;
	text-shadow: 1px 1px 4px rgba(0,0,0,.2);
}
#main .controller a.prev,
#main .controller a.next {
	display: inline-block;
	position: relative;
	top: 1px;
	z-index: 1;
	width: 15px;
	height: 44px;
	vertical-align: middle;
}
#main .controller a.prev {
	background: url(/src/css/images/btn_prev.png) no-repeat 50% 50%;
	opacity: 0.6;
}
#main .controller a.next {
	background: url(/src/css/images/btn_next.png) no-repeat 50% 50%;
	opacity: 0.6;
}
#main .controller a.prev:hover,
#main .controller a.next:hover {
	opacity: 1;
}
#main .controller .count,
#main .controller .count li {
	display: inline-block;
	vertical-align: middle;
}
#main .controller .count {
	margin: 0 8px;
}
#main .controller .count li a {
	display: block;
	margin: 0 10px -1px 10px;
	width: 4px;
	height: 4px;
	overflow: hidden;
	text-indent: -9999px;
	background-color: rgba(255,255,255,.5);
	box-shadow: 1px 1px 4px #000;
	border-radius: 50%;
}
#main .controller .count li.active a {
	background-color: #fff;
}
#main .controller svg {
	position: absolute;
	top: 14px;
	width: 20px;
	height: 20px;
	overflow: visible;
	transform: rotate(-90deg);
}
#main .controller svg circle {
	fill: none;
	stroke: #fff;
	stroke-width: 2px;
	stroke-dasharray: 62.832;
	stroke-dashoffset: 62.832;
	text-shadow: 1px 1px 4px #000;
}
#main .controller.active svg circle {
	animation: stoke_animation 2s;
	animation-fill-mode: both;
}
#main .controller .num {
	display: inline-block;
	margin-left: 20px;
	vertical-align: middle;
	font-size: 0.75em;
	text-shadow: 1px 1px 4px #000;
	color: #fff;
}
#main .controller .num span {
	font-weight: 500;
}
@keyframes stoke_animation {
	from {
		stroke-dashoffset: 62.832;
	}
	to {
		stroke-dashoffset: 0;
	}
}
#main .inner {
	position: absolute;
	z-index: 10000;
	left: 0;
	top: 55%;
	padding: 0 30px;
	width: 100%;
	box-sizing: border-box;
	transform: translateY(-50%);
}
#main .inner .title h2 {
	font-family: "Roboto Condensed", sans-serif;
	line-height: 1.4;
	font-size: 5.5em;
	font-weight: 100;
	color: #fff;
}
#main .inner .title h2 span {
	font-weight: 700;
}
#main .inner .sub_title {
  position: absolute;
	z-index: 999999;
	right: 45px;
	bottom: 23%;
	line-height: 2;
	text-align: right;
	font-size: 1.15em;
	font-weight: 400;
	letter-spacing: -1px;
	color: #fff;
	transition: 3s;
	opacity: 0;
}
#main .inner .sub_title span {
	display: block;
}
#main .inner .skills {
	margin-top: 15vh;
}
#main .inner .skills.invisible {
	display: none;
}
#main .inner .skills ul {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	gap: 30px;
	transition: 3s;
	opacity: 0;
}
#main .inner .skills ul li .number {
	line-height: 2;
	font-size: 1em;
	font-weight: 300;
	color: #fff;
}
#main .inner .skills ul li span {
	font-weight: 700;
	font-size: 0.913em;
	color: #fff;
}
#main .dim {
	position: absolute;
	z-index: 9999;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.7);
}
@media only screen and (max-width: 1270px){
	#main .inner .sub_title {font-size: 1em;}
}
@media only screen and (max-width: 1150px){
	#main .inner .sub_title {font-size: 0.88em;}
	.br {display: none;}
}
@media only screen and (max-width: 970px){
	#tab {display: block;}
	#gnb ul {display: none;}
	header .inner .logo {padding-left: 0;}
	#main .inner .sub_title {width: 30%; right: 50px;}
	#main .inner .title h2 {font-size: 4em;}
	#main .inner .skills ul li .number {font-size: 0.88em;}
}
@media only screen and (max-width: 760px){
	#main .inner .sub_title {display: none;}
}
@media only screen and (max-width: 510px){
	#main .inner .title h2 {font-size: 3em;}
	#main .controller {display: none;}
	#main .inner .skills {margin-top: 5vh;}
	#main .inner .skills ul {flex-direction: column; align-items: flex-start; gap: 10px;}
	#main .inner .skills ul li .number {font-size: 0.69em;}
	#main .inner .skills ul li .number {display: none;}
	#main .controller {bottom: 330px;}
	
}

