.opensource {
	max-width: 1240px;
	margin: 20vh auto 30px;
	box-sizing: border-box;
	/* transition: 3s;
	opacity: 0; */
}

.opensource html,
.opensource body {
  position: relative;
  height: 100%;
}
.opensource body {
  background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}
.opensource h2 {
	padding: 0 0 25px;
	font-size: 3.5em;
	font-weight: bold;
	text-align: right;
	border-bottom: 3px solid #000;
}
.opensource .swiper {
	padding-bottom: 10px;
	margin-top: 100px;
	/* overflow: visible; */
}
.opensource .mySwiper {
	width: 100%;
	height: 100%;
}
.opensource .swiper-slide .img {
	position: relative;
	width: 100%;
	height: 160px;
}
.opensource .swiper-slide .img a:after {
	position: absolute;
	content: "";
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	transition: background-color 0.5s;
}
.opensource .swiper-slide:hover .img a:after {
	background-color: transparent;
}
.opensource .swiper-slide .img img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.opensource .swiper-slide .desc {
	padding: 20px 10px;
}
.opensource .swiper-slide .desc p {
	font-weight: 500;
	font-size: 1.1em;
}
.opensource .swiper-slide .desc > span {
	display: block;
	padding-top: 10px;
	height: 120px;
}
.opensource .swiper-slide .desc .reference {
	margin-top: 15px;
	padding-top: 5px;
	border-top: 1px solid rgba(0,0,0,.2);
}
.opensource .swiper-slide .desc .reference li {
	white-space: nowrap;
}
.opensource .swiper-slide .desc .reference li span {
	display: inline-block;
	vertical-align: middle;
	padding-right: 10px;
	line-height: 2.2;
	font-size: 0.7rem;
}
.opensource .swiper-slide .desc .reference li a {
	display: inline-block;
	vertical-align: middle;
	width: calc(100% - 40px);
	line-height: 2.2;
	font-size: 0.75rem;

	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}

